.base {
  padding-top: 170px;
  min-height: 600px;
  @include big-desktop {
    padding-top: 100px;
  }
  @include small-tablet {
    padding-top: 50px;
  }
  @include mobile {
    padding-top: 0px;
  }

  &__title {
    text-align: left;
    @include big-desktop {
      text-align: center;
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 50px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    @include big-desktop {
      text-align: center;
    }
  }
  &__wrapper {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    row-gap: 30px;
    column-gap: 30px;
    @include big-desktop {
      justify-content: center;
    }
  }
}

.base-card {
  position: relative;
  padding: 45px 59px 53px 55px;
  width: 571px;
  min-height: 414px;
  background-color: rgb(248, 249, 251);
  @include big-desktop {
    width: 450px;
  }
  @include tablet {
    width: 350px;
    padding: 20px 20px 20px 20px;
  }
  @include mobile {
    padding: 10px 10px;
  }
  &__title {
    margin: 0;
    margin-bottom: 16px ;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: 21px;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: 1%;
    text-align: left;
    @include big-desktop {
      text-align: center;
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 36px ;
    color: rgb(130, 133, 141);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
    @include big-desktop {
      text-align: center;
    }
  }
  &__schedule {
    position: relative  ;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 500;
    line-height: 23px;
    text-align: left;
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      left: -49px;
      width: 34px;
      height: 34px;
    }
    &-1::before {
      background-image: url('../img/base/base-1.png');
    }
    &-2::before {
      background-image: url('../img/base/base-2.png');
    }
  }
  &__churt {
    margin-bottom: 46px;
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    height: 88px;
    row-gap: 33px;
    @include mobile {
      height: 180px;
    }
  }
  &__item {
    margin-bottom: 43px;
    display: flex;
    height: 85px;
    width: 310px;
    align-items: center;
    justify-content: space-between;
  }
  &__round {
    width: 85px;
    height: 85px;
    border-radius: 100%;
    background: rgb(223, 228, 239);
  }
  &__img {
    width: 100%;
    border-radius: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__name {
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 500;
    line-height: 23px;
    text-align: left;
  }
  &__post {
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 300;
    line-height: 23px;
    text-align: left;
  }
  &__link {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 20px;
    left: 53px;
    padding: 16px 25px;
    width: 338px;
    height: 47px;
    color: rgb(43, 125, 224);
    font-family: Montserrat;
    font-size: 12px;
    font-weight: 500;
    line-height: 15px;
    text-align: center;
    @include tablet {
      width: 275px;
      left: 50%;
      transform: translateX(-50%);
    }
    @include mobile {
      width: 300px;
      left: 50%;
      transform: translateX(-50%);
      bottom: 40px ;
    }
  }
}

