.need {
  margin-bottom: 140px;
  @include mobile {
    margin-bottom: 40px;
  }
  &__container {
    padding-top: 51px;
    padding-bottom: 61px;
    flex-direction: column;
    align-items: center;
    min-height: 478px;
    border: 2px solid rgb(223, 228, 239);
    border-radius: 5px;
  }

  &__wrapper {
    flex-direction: column;
    width: 723px;
    @include small-tablet {
      width: 80%;
    }
    @include mobile {
      width: 100%;
    }
  }

  &__title {
    text-align: center;
    margin: 0;
    margin-bottom: 22px;
    @include small-tablet {
      margin-bottom: 10px;
      font-size: 30px;
    }
    @include mobile {
      font-size: 25px;
  }
  }

  &__descr {
    margin: 0;
    margin-bottom: 36px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 400;
    line-height: 26px;
    text-align: center;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    background-color: var(--main-color);
    color: var(--light-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 500;
    line-height: 15px;
    letter-spacing: 1%;
    text-align: center;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color);
      background-color: var(--black-color);
    }

    &:active {
      color: var(--light-color);
      background-color: var(--black-color);
    }
  }

  .need__btn.focus-visible {
    outline: 1px solid var(--black-color);
  }


  // формы
  &__form {
    color: rgb(160, 166, 178);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
    display: flex;
    gap: 20px;
    height: 181px;
    @include small-tablet {
      flex-direction: column;
      justify-content: center;
      min-height: 400px;
    }
  }

  &__left {
    display: flex;
    width: 478px;
    height: 181px;
    @include small-tablet {
      width: 100%;
    }
  }

  &__textarea {
    background-color: rgb(239, 241, 245);
    resize: none;
    width: 100%;
    height: 100%;
    padding: 17px 24px;
    border: none;
  }

  &__right {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-direction: column;

    input {
      height: 47px;
      padding: 11px 24px;
      background-color: rgb(239, 241, 245);
      border: none;
    }

    button {
      height: 47px;
    }
  }
}
