.vision {
  padding-top: 100px;
  @include mobile {
    padding-top: 40px;
  }
  &__wrapper {
    width: 974px;

    @include tablet {
      width: 100%;
    }
  }
  &__title {
    text-align: left;
  }
  &__text {
    padding-right: 162px;
    margin: 0;
    margin-bottom: 53px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 500;
    line-height: 26px;
    text-align: left;

    @include tablet {
      padding-right: 50px;
    }

    @include small-tablet {
      padding-right: 0;
    }
  }

}

// Отдельный блок видение

.vision-list {
  counter-reset: steps;
  flex-direction: column;
  row-gap: 15px;

  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    padding-left: 90px;
    width: 974px;
    min-height: 58px;
    background-color: var(--back-color);
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 600;
    line-height: 23px;
    letter-spacing: 0%;
    text-align: left;

    &::before {
      position: absolute;
      content: '';
      counter-increment: steps;
      content: counter(steps);
      top: 50%;
      transform: translateY(-50%);
      left: 29px;
      color: rgb(235, 239, 247);
      font-size: var(--big-size);
      font-weight: 700;
      @include mobile {
        top: 20px;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    @include tablet {
      width: 100%;
    }
    @include mobile {
      justify-content: center;
      align-items: center;
      padding-left: 0;
      padding-top: 60px;
      padding-bottom: 10px;
      text-align: center;
    }
  }
  @include mobile {
    row-gap: 50px;
  }
}
