.help {
  padding-top: 90px;
  padding-bottom: 90px;
  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__container {
    justify-content: space-between;
    align-items: flex-start;
    column-gap: 117px;
    @include tablet {
      flex-direction: column;
    }
  }
  &__right {
    margin: 0 auto;
    @include tablet {
      margin-top: 40px;
    }
    @include mobile {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }
  &__title {
    text-align: left;
    @include mobile {
      text-align: center;
    }
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 47px;
    color: rgb(51, 51, 51);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    @include small-tablet {
      br {
        display: none;
      }
    }
    @include mobile {
      margin-bottom: 100px;
      text-align: center;
      br {
        display: none;
      }
    }
  }

  // блок текста с иконками
  &__list {
    display: flex;
    flex-direction: column;
    padding-left: 98px;
    row-gap: 36px;
    @include mobile {
      padding-left: 0;
      row-gap: 100px;
    }
  }
  &__item {
    position: relative;
    &-1 {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -92px;
        width: 65px;
        height: 62.49px;
        background-image: url('../img/help/help_1.png');
        background-repeat: no-repeat;
        @include mobile {
          left: 50%;
          transform: translateX(-50%);
          top: -80px ;
        }
      }
    }
    &-2 {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -98px;
        width: 65px;
        height: 68.49px;
        background-image: url('../img/help/help_2.png');
        background-repeat: no-repeat;
        @include mobile {
          left: 50%;
          transform: translateX(-50%);
          top: -80px ;
        }
      }
    }
    &-3 {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -89px;
        width: 65px;
        height: 62.49px;
        background-image: url('../img/help/help_3.png');
        background-repeat: no-repeat;
        @include mobile {
          left: 50%;
          transform: translateX(-50%);
          top: -80px ;
        }
      }
    }
  }
  &__advantages {
    margin: 0;
    margin-bottom: 7px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 600;
    line-height: 23px;
    text-align: left;
    @include mobile {
      text-align: center;
    }
  }
  &__descr {
    margin: 0;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
    @include mobile {
      text-align: center;
    }
  }

  &__img {
    border-radius: 20px;
    width: 400px;
    height: 500px;
    @include mobile {
      width: 300px;
      height: 400px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    width: 399px;
    height: 73px;
    color: rgb(43, 125, 224);
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 5%;
    text-align: center;
    @include mobile {
      width: 300px;
    }
  }

}

