.about {
  padding-top: 100px;
  @include mobile {
    padding-top: 40px;
  }
  &__wrapper-top {
    width: 820px;
    @include tablet {
      width: 100%;
    }
  }
  &__wrapper-bot {
    width: 1000px;
    @include tablet {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
    }
    @include small-tablet {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  &__title {
    text-align: left;
  }
  &__descr {
    margin: 0;
    margin-bottom: 86px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    @include small-tablet {
      margin-bottom: 40px;
    }
  }
  &__text {
    margin: 0;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
    margin-bottom: 144px;
    @include small-tablet {
      margin-bottom: 70px;
    }
  }

  &__link {
    padding: 0;
    border: none;
    color: inherit;
    text-decoration: underline;
    transition: color 0.3s ease-in-out;
    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--main-color);
      background-color: var(--light-color);
    }

    &:active {
      color: var(--main-color);
      background-color: var(--light-color);
    }
  }
  .about__link.focus-visible {
    outline: 1px solid var(--black-color);
  }



  &__list {
    width: 1200px;
    display: flex;
    flex-direction: column;
    padding-right: 200px;
    gap: 50px;
    @include tablet {
      width: 760px;
      padding-right: 50px;
    }
    @include small-tablet {
      width: 560px;
      padding-right: 0px;
    }
  }

  &__item {
    display: flex;
    gap: 50px;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
    }
  }
  &__img {
    width: 140px;
    height: 140px;
    background-color: rgb(223, 228, 239);
    border-radius: 100%;
    flex-shrink: 0;
    // background: url('../img/about/about.jpg') center center/cover no-repeat;
    @include small-tablet {
    }
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 8px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 600;
    line-height: 23px;
    text-align: left;
  }
  &__info {
    margin: 0;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
  }
}
