.device {
  padding-top: 100px;
  padding-bottom: 50px;
  @include mobile {
    padding-top: 40px;
  }
  &__wrapper {
    width: 910px;
    @include tablet {
      width: 100%;
    }
  }
  &__title {
    margin-bottom: 30px;
    padding-right: 282px;
    @include tablet {
      padding-right: 150px;
    }
    @include mobile {
      padding-right: 0;
      text-align: center;
    }
  }
  &__text {
    margin: 0;
    margin-bottom: 45px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    @include mobile {
      text-align: center;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    row-gap:90px;
    @include small-tablet {
      row-gap: 45px;
    }
  }
  &__descr {
    margin: 0;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    text-align: left;
    @include mobile {
      text-align: center;
    }
  }
}
