.welcome {
  position: relative;
  padding-top: 90px;
  padding-bottom: 160px;
  min-height: 580px;
  background: url('../img/background.jpg') center center/cover no-repeat;
  @include big-desktop {
    min-height:300px;
    padding-bottom: 100px;
  }
  @include small-tablet {
    padding-top: 90px;
    padding-bottom: 80px;
    min-height: 400px;
  }
  @include mobile {
    padding-top: 40px;
    margin-bottom: 40px;
  }

  &__container {
    @include mobile {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  &__wrapper {
    width: 765px;
    @include small-tablet {
      width: 550px;
    }
    @include mobile {
      // margin: 0 auto;
      text-align: center;
      width: 320px;
    }
  }
  &__title {
    @include mobile {
      text-align: center;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    padding-right: 165px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 23px;
    @include small-tablet {
      margin-bottom: 30px;
      padding-right: 0;
  }
    @include mobile {
      padding-right: 0px;
  }
  }

  &__items {
    width: 500px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 20px;
    @include mobile {
      justify-content: center;
      align-items: center;
      width: 320px;
      row-gap: 20px;
    }
  }

  &__btn {
    color: var(--main-color);
    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.12px;
  }

  &__link {
    min-width: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    color: var(--main-color);
    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.12px;
    @include mobile {
      width: 235px;
    }
  }

  &__bottom {
    position: absolute;
    width: 1200px;
    height: 140px;
    bottom: -70px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--light-color);
    border-radius: 5px;
    box-shadow: 8px 7px 15px 0px rgba(0, 0, 0, 0.05);
    @include big-desktop {
      display: none;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 40px;
    @include big-desktop {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }

  &__item {
    padding-right: 140px;
    width: 360px;
    min-height: 74px;
    border-right: 1px solid #E9EAEC;

    &:last-child {
      border: none;
    }
    @include big-desktop {
      border: none;
    }
  }


  &__subtitle {
    position: relative;
    margin: 0;
    color: var(--, #333);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-style: normal;
    font-weight: 500;
    line-height: 26px;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -100px;
      width: 67px;
      height: 67px;
      border-radius: 100%;
      flex-shrink: 0;
      background: #DFE4EF;
      // background-image: url('../img/');
    }
  }
}
