.tablet {
  padding-top: 90px;
  padding-bottom: 90px;
  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__title {
    text-align: left;
  }

  &__text {
    margin: 0;
    margin-bottom: 53px;
    padding-right: 162px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 500;
    line-height: 26px;
    text-align: left;
    @include tablet {
      padding-right: 50px;
    }
    @include small-tablet {
      padding-right: 0;
    }
  }
}
