.grow {
  color: white;
  padding-top: 100px;
  font-size: var(--big-size);
  @include mobile {
    padding-top: 40px;
  }
  &__title {
    @include mobile {
      text-align: center;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 53px;
    padding-right: 195px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--middle-size);
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0%;
    text-align: left;
    @include tablet {
      padding-right: 50px;
    }
    @include small-tablet {
      padding-right: 0;
    }
    @include mobile {
      text-align: center;
    }
  }
}

.grow-cards {
  display: flex;
  gap: 45px;
  flex-wrap: wrap;
  justify-content: center;
  &__item {
    padding:48px 45px 42px 45px ;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 370px;
    min-height: 308px;
    border-radius: 5px;
    background: rgb(248, 249, 251);
    @include mobile {
      width: 320px;
    }
  }
  &__img {
    margin-bottom: 25px;
  }
  &__title {
    margin: 0;
    margin-bottom: 12px;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: 21px;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: 1%;
    text-align: center;
  }
  &__descr {
    margin: 0;
    color: var(--black-color);
    font-family: Montserrat;
    font-size: var(--small-size);
    font-weight: 500;
    line-height: 23px;
    text-align: center;
  }
}
